import PropTypes from 'prop-types';
import { alpha } from '@mui/material/styles';
import Stack from '@mui/material/Stack';
import Dialog from '@mui/material/Dialog';
import DialogTitle from '@mui/material/DialogTitle';
import DialogContent from '@mui/material/DialogContent';
import Typography from '@mui/material/Typography';
import Authenticate from 'hocs/require-authentication';
import CircularProgress from '@mui/material/CircularProgress';
import PendingIcon from '@mui/icons-material/Pending';
import CheckCircleOutlineIcon from '@mui/icons-material/CheckCircleOutline';
import CloseIcon from '@mui/icons-material/Close';

function StatusIndicator({ status }) {
  if (status === 'running') {
    return <CircularProgress color="info" size="1rem" />;
  }
  if (status === 'pending') {
    return <PendingIcon color="disabled" size="0.875rem" />;
  }
  if (status === 'done') {
    return <CheckCircleOutlineIcon color="success" size="0.875rem" />;
  }
  if (status === 'failed') {
    return <CloseIcon color="error" size="0.875rem" />;
  }
}

StatusIndicator.propTypes = {
  status: PropTypes.string.isRequired,
};

function Status({ text, status }) {
  const color = {
    pending: alpha('#000', 0.26),
    done: 'black',
    running: 'black',
    failed: 'red',
  };

  return (
    <Stack direction="row" justifyContent="space-between">
      <Typography
        sx={{ fontWeight: 'bold', fontSize: '0.875rem', color: color[status] }}
      >
        {text}
      </Typography>
      <StatusIndicator status={status} />
    </Stack>
  );
}

Status.propTypes = {
  text: PropTypes.string.isRequired,
  status: PropTypes.string.isRequired,
};

export default function SaveStatusDialog({ open, status }) {
  return (
    <Authenticate>
      <Dialog open={open} fullWidth>
        <DialogTitle sx={{ textAlign: 'center' }}>Save Collage</DialogTitle>
        <DialogContent dividers>
          <Stack direction="column" justifyContent="center" spacing={2}>
            {Object.values(status).map((st) => (
              <Status key={st.text} text={st.text} status={st.status} />
            ))}
          </Stack>
        </DialogContent>
      </Dialog>
    </Authenticate>
  );
}

SaveStatusDialog.propTypes = {
  open: PropTypes.bool.isRequired,
  status: PropTypes.arrayOf(PropTypes.objectOf(PropTypes.string)).isRequired,
};
